$ColorLightBlue: #D5F5F9;
$ColorDarkBlue: #00659D;
$ColorNaranja: #fc8890;

.wave {
    position: fixed;
    height: 100%;
    left: 0;
    bottom: 0;
    z-index: -1;
}

.container-login {
    // width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 7rem;
    padding: 0 2rem;
}

.img-login {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.img-login>img {
    width: 500px;
}

.login-container {
    display: flex;
    align-items: center;
    text-align: center;
}

.avatar {
    width: 250px;
}

.form-login>h2 {
    font-size: 2.9rem;
    text-transform: uppercase;
    margin: 15px 0;
    color: $ColorDarkBlue;
}

.input-div {
    position: relative;
    display: grid;
    grid-template-columns: 7% 93%;
    margin: 25px 0;
    padding: 5px;
    border-bottom: 2px solid $ColorDarkBlue;
}

.input-div:after,
.input-div:before {
    content: "";
    position: absolute;
    bottom: -2px;
    width: 0;
    height: 2px;
    background-color: $ColorLightBlue;
    transition: 0.3s;
}

.input-div:after {
    right: 50%;
}

.input-div:before {
    left: 50%;
}

.input-div.focus .i i {
    color: $ColorLightBlue;
}

.input-div.focus div h5 {
    top: -5px;
    font-size: 15px;
    color: $ColorLightBlue;
}

.input-div.focus:after,
.input-div.focus:before {
    width: 50%;
}

.input-div.one {
    margin-top: 0;
}

.input-div.two {
    margin-bottom: 4px;
}

.input-feedback {
    width: -webkit-fill-available;
    color: red;
    margin-top: 0.25rem;
}

.i {
    display: flex;
    justify-content: center;
    align-items: center;
}

.i i {
    color: $ColorDarkBlue;
    transition: 0.3s;
}

.input-div>div {
    position: relative;
    height: 45px;
}

.input-div>div h5 {
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
    color: $ColorDarkBlue;
    font-size: 18px;
    transition: 0.3s;
}

.input {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border: none;
    outline: none;
    background: none;
    padding: 0.5rem 0.7rem;
    font-size: 1.2rem;
    font-family: "Poppins", sans-serif;
    color: #555;
}

.input-check {
    // position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 100px;
}

.olvidado {
    display: block;
    text-align: right;
    text-decoration: none;
    color: #999;
    font-size: 0.9rem;
    transition: 0.3s;
    font-family: "Poppins", sans-serif;
}

.olvidado:hover {
    color: $ColorDarkBlue;
}

.btn-login {
    display: block;
    width: 100%;
    height: 50px;
    border-radius: 25px;
    margin: 1rem 0;
    font-size: 1.2rem;
    outline: none;
    border: none;
    background-image: linear-gradient(to right, $ColorNaranja, $ColorNaranja, $ColorNaranja);
    cursor: pointer;
    color: #fff;
    text-transform: uppercase;
    font-family: "Poppins", sans-serif;
    background-size: 200%;
    transition: 0.5s;
}

.btn-login:hover {
    background-position: right;
}

@media screen and (max-width: 1050px) {
    .container-login {
        grid-gap: 5rem;
    }
}

@media screen and (max-width: 1000px) {
    form-login {
        width: 290px;
    }

    form-login h2 {
        font-size: 2.4rem;
        margin: 8px;
    }

    .img-login img {
        width: 400px;
    }
}

@media screen and (max-width: 900px) {
    .img-login {
        display: none;
    }

    .container-login {
        grid-template-columns: 1fr;
    }

    .wave {
        display: none;
    }

    .login-container {
        justify-content: center;
    }
}

.error {
    padding: 25px;
    font-size: 12px;
    color: #e53e3e;
    margin-top: 0.25rem;
}

@media (prefers-color-scheme: dark) {
    .error {
        color: #fc8181;
    }
}

.text-center {
    text-align: center !important;
}